$(document).ready(function() {
    fix100vh();

    new AirDatepicker('.date-input', {
        locale: localeRu,
        // inline: true,
        // isMobile: true,
        startDate: new Date(),
        autoClose: true,
        onSelect: () => {
            $(".date-input").addClass("input-active")
        }
    })

});


fix100vh();
function fix100vh() {
    let vh = window.innerHeight * 0.01;
    document.documentElement.style.setProperty('--vh', `${vh}px`);
}
